@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@function size($size) {
  $remSize: $size / $font-size-main;
  @return #{$remSize}rem;
}

@mixin reset-btn {
  padding: 0;
  margin: 0;
  background: $color-transparent;
  color: inherit;
  text-decoration: none;
  outline: none;
  border: none;
  border-radius: 0;
  cursor: pointer;
}

@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

// Desktop first

@mixin vp--1919 {
  @media (max-width: $vp--1920 - 1) {
    @content;
  }
}

@mixin vp--1439 {
  @media (max-width: $vp--1440 - 1) {
    @content;
  }
}

@mixin vp--1279 {
  @media (max-width: $vp--1280 - 1) {
    @content;
  }
}

@mixin vp--1023 {
  @media (max-width: $vp--1024 - 1) {
    @content;
  }
}

@mixin vp--767 {
  @media (max-width: $vp--768 - 1) {
    @content;
  }
}

@mixin vp--374 {
  @media (max-width: $vp--375 - 1) {
    @content;
  }
}

