.theme-switcher {
  position: relative;
  background-color: rgba(118, 109, 244, 0.3);
  background-color: var(--primary-color-30);
  border-radius: 0.75rem;
  transition: background-color .2s, box-shadow .2s; }
  .theme-switcher__toggle {
    width: 4.25rem;
    height: 2.125rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 2px solid #766DF4;
    border: 2px solid var(--primary-color);
    border-radius: 0.75rem;
    padding: 0.25rem;
    color: #ffffff;
    color: var(--white-color);
    cursor: pointer;
    transition: border-color .2s; }
    .theme-switcher__toggle svg {
      width: 1.5rem;
      height: 1.5rem;
      z-index: 1;
      pointer-events: none; }
    .theme-switcher__toggle::after {
      content: "";
      position: absolute;
      border-radius: 0.75rem;
      left: 0;
      top: 0;
      height: 100%;
      width: 36px;
      background-color: #766DF4;
      background-color: var(--primary-color);
      pointer-events: none;
      transition: transform .3s, background-color .2s; }
  .theme-switcher input:checked + .theme-switcher__toggle::after {
    transform: translateX(2rem); }
  .theme-switcher input:focus + .theme-switcher__toggle {
    box-shadow: 0px 0px 0px 3px rgba(118, 109, 244, 0.25); }
  @media (min-width: 1024px) {
    .theme-switcher:hover .theme-switcher__toggle {
      border-color: #5549F1;
      border-color: var(--primary-color-hover); }
      .theme-switcher:hover .theme-switcher__toggle::after {
        background-color: #5549F1;
        background-color: var(--primary-color-hover); } }

